.SliderItem {
    background: #f5f5f5;
    height: 280px;
    max-width: 520px;
    padding: 25px;
    @media (max-width: 1160px) {
        height: 300px;
        width: 450px;
        padding: 22px;
    }
    @media (max-width: 925px) {
        height: 322px;
        width: 375px;
    }
    @media (max-width: 780px) {
        width: 289px;
    }
    @media (max-width: 576px) {
        height: 348px;
        width: 272px;
    }
    &__wrapper {
        overflow: hidden;
        align-items: start;
        margin-bottom: 25px;
        &__top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: nowrap;
            flex-direction: row;
            &__img {
                height: 52px;
                width: 52px;
                margin-right: 20px;
            }
            &__name {
                font-size: 24px;
                line-height: 28px;
                color: #333;
                font-weight: 500;
                font-style: normal;
                font-family: 'Factor A';
            }
            &__date {
                font-family: 'Factor A';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 22px;
                color: #8a8a8a;
            }
        }
    }
    &__text {
        color: #333;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        font-family: 'Gilroy';
    }
}
