.main {
    background-color: #585cc6;
    padding-bottom: 106px;
    padding-top: 93px;
    background-image: url(../assets/img/1vector.png);
    background-position: top -10px right 5px;
    background-repeat: no-repeat;
    @media (max-width: 767px) {
        padding-bottom: 83px;
        padding-top: 75px;
    }
    @media (max-width: 576px) {
        padding-bottom: 75px;
        padding-top: 68px;
    }
    &__welcome {
        margin: 93px 203px 0px 80px;
        @media (max-width: 576px) {
            margin: 77px 15px 0px 16px;
        }

        h1 {
            font-size: 124px;
            font-weight: 500;
            font-style: normal;
            letter-spacing: -1.24%;
            line-height: 148px;
            color: #fff;
            font-family: 'Factor A';
            @media (max-width: 576px) {
                font-size: 48px;
                line-height: 54px;
            }
        }
    }

    .userInfo {
        display: flex;
        margin: 88px 80px 120px 80px;
        @media (max-width: 990px) {
            margin: 75px 45px 87px 45px;
            display: block;
        }
        @media (max-width: 576px) {
            margin: 64px 16px 57px 16px;
            display: block;
        }
        &__photoUser {
            height: 383px;
            width: 339px;
            margin-top: 32px;
            @media (max-width: 576px) {
                height: 370px;
                width: 330px;
                margin: 0;
            }
        }
        &__info {
            height: 383px;
            width: 761px;
            margin-bottom: 32px;
            background: #fff;
            padding: 40px;
            @media (max-width: 1210px) {
                height: 482px;
                width: 551px;
                padding: 32px;
            }
            @media (max-width: 995px) {
                height: 460px;
                width: 551px;
                padding: 32px;
            }

            @media (max-width: 576px) {
                padding: 16px;
                height: 530px;
                width: 288px;
                top: 988px;
                left: 15px;
            }

            &__top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                @media (max-width: 576px) {
                    display: block;
                    margin-bottom: 16px;
                }

                &__name {
                    color: #585cc6;
                    font-size: 32px;
                    font-weight: 700;
                    font-style: normal;
                    line-height: 32px;
                    font-family: 'Factor A';
                    @media (max-width: 576px) {
                        font-size: 24px;
                        line-height: 32px;
                    }
                }
                &__date {
                    color: #8a8a8a;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 24px;
                    margin: 0;
                    font-family: 'Gilroy';
                    @media (max-width: 576px) {
                        font-size: 16px;
                        line-height: 22px;
                    }
                }
            }

            &__info {
                display: flex;
                flex-wrap: wrap;
                grid-column-gap: 40px;
                color: #333333;
                font-size: 18px;
                line-height: 24px;
                letter-spacing: 0.01em;
                margin: 20px 0px 18px 0px;
                font-family: 'Gilroy';
                @media (max-width: 576px) {
                    font-size: 16px;
                    line-height: 22px;
                }
            }

            &__text {
                a {
                    font-family: 'Gilroy';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 24px;
                    letter-spacing: 0.01em;
                    color: #333333;
                    span {
                        font-weight: normal;
                    }
                    @media (max-width: 576px) {
                        font-size: 16px;
                        line-height: 22px;
                    }
                }
            }

            &__icon {
                display: flex;
                grid-column-gap: 15px;
                margin-top: 35px;
                @media (max-width: 576px) {
                    margin-top: 15px;
                }
                a {
                    font-family: 'Gilroy';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 24px;
                    letter-spacing: 0.01em;
                    color: #333333;
                }
            }
        }
    }
}
